import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the SharingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sharing',
  templateUrl: 'sharing.html',
})
export class SharingPage {

  data = {
    topic: "",
    when: "",
    where: "",
    who: "",
    sharing: "",
    help: "",
    person: null
  }

  hasError = false;
  errMsg = ""

  allowBack = true;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private service: ServiceProvider) {

      this.data.person = this.navParams.get('person')

      if(!navParams.get('actived')) {
        navCtrl.setRoot('BerandaPage')
      }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SharingPage');
  }

  ionViewWillEnter() {
    if(this.service.sharing_page) {
      this.navCtrl.popToRoot()
    }
  }

  async keHalamanEnd() {
    this.hasError = false;

    if(
      this.data.topic &&
      this.data.when &&
      this.data.where &&
      this.data.who &&
      this.data.sharing &&
      this.data.help
    ) {

      this.service.LOADER(); // loading global
      console.log('data', this.data)

      
      await this.service.simpanDataSharing(this.data).then((res:any) => {
        console.log('data push', res)

        var sharing = this.navParams.get('person')
        console.log('sharer', sharing);
        sharing.save({sharing: {__type: "Pointer", className: "Sharing", objectId: res.objectId }})

        this.navCtrl.push('EndPage')
        this.service.Loader.dismiss()

        this.service.sharing_page = true;

      }, err => {
        console.log(err); 
        this.service.Loader.dismiss()
      })

    }
    else {
      console.log('data harus diisi')
      this.errMsg = "Pastikan semua sudah terisi yaaa..."
      this.hasError = true;
    } 

    // console.log('keHalamanEnd')
  }
}



